.tab-list {
    width: 5%;
    height: 100%;   
    position: fixed;
    margin: 0;
    padding: 0;
    background-color: #333;
}

.tab-list-item {
    display: block;
    list-style: none;
    color: #ddd;
    padding: 2rem 0rem;
}

.tab-list-item:hover {
    cursor: pointer;
    background-color: #aaa;
    color: #666;
}

.tab-list-active {
    background-color: #999;
}

.tab-content {
    padding: 1rem;
    margin-left: 5%;
    background-color: #555;
}

  
  